@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
}

body {
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #53dfd1;
  color: #343a40;
}

.App{width:100vw;height:100vh;display:flex;justify-content:center;align-items:center}
.displayTable{width:70%;height:90%;position:relative}.event-heading{text-align:center;font-size:2rem;padding:10px;height:8%}.loader{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}.ranking-table{position:relative;width:100%;height:calc(92% - 20px)}@media(max-width: 991.98px){.displayTable{width:85%}.event-heading{font-size:1.8rem}}@media(max-width: 767.98px){.displayTable{width:95%}.event-heading{font-size:1.6rem}}@media(max-width: 575.98px){.displayTable{width:97%;height:90%}.event-heading{font-size:1.4rem}}
.main-table{width:100%;height:100%}.table-headers{height:2%;width:calc(100% - 20px);padding:10px;text-transform:uppercase;display:flex;font-weight:700;font-size:.75rem}.header-text{text-decoration:underline}.table-body{width:100%;height:46.6%;overflow:scroll}.winner-table{height:98%;width:100%}.table-content{background-color:#e0ebe5;padding:10px;height:50px;width:calc(100%-20px);display:flex;font-size:1.2rem}.table-content:nth-of-type(2n){background-color:#b8ddd6}.table-content-ranking{width:20%;display:flex;align-items:center;justify-content:center}.table-content-name{width:40%;display:flex;align-items:center}.table-content-startTime{width:20%;display:flex;align-items:center}.table-content-finishTime{width:20%;display:flex;align-items:center}.medal{width:25%;height:120%}.addMargin{margin-bottom:15px;border-radius:5px;height:65px;background-color:#fff !important;border:.5px solid #000c66 !important}@media(max-width: 991.98px){.table-content{font-size:1rem;height:40px}.table-body{height:36.3%}.addMargin{height:50px}.medal{width:20%}}@media(max-width: 767.98px){.table-content{font-size:.9rem;height:30px}.table-body{height:34.6%}.addMargin{height:40px}.medal{width:30%}}@media(max-width: 575.98px){.table-headers{font-size:.6rem}.table-content{font-size:.6rem;height:15px}.table-body{height:31.1%}.addMargin{height:25px}.medal{width:25%;height:130%}}
